import Vue from "vue";
import "./index.css";
import httputils from "@/js/httputils";
import choosenights from "./components/choosenights";
import choosehotelresource from "./components/choosehotelresource";
import uploadimage from "@/components/uploadimage";
const app = new Vue({
  el: "#formSub",
  components: {
    choosenights,
    choosehotelresource,
    uploadimage
  },
  data() {
    return {
      contactsource: [],
      producttype: $("#producttype").val(),
      getFormData: {
        GroupPlaceType: 0,
        GroupTravelType: 0,
        IsJoinGroup: -1,
        GroupDetail: {
          RadioValue: 0,
          IsChecked: false,
          JoinGroupNum_1: 0,
          JoinGroupNum_2: 0,
          JourneyRemark: ""
        },
        Additional: {
          IsChecked_1: false,
          Remark_1: "",
          IsChecked_2: false,
          Remark_2: "",
          IsChecked_3: false,
          Remark_3: ""
        },
        ContactsList: [{
            Type: 1,
            LinkerId: 0,
            ContactName: "",
            ContactMobile: ""
          },
          {
            Type: 2,
            LinkerId: 0,
            ContactName: "",
            ContactMobile: ""
          }
        ],
        HotelList: [],
        TrafficList: [],
        VisaInfo: {
          VisaType: 0,
          PrePareInfoList: [],
          IsChecked_1: false,
          IsChecked_2: false,
          IsChecked_3: false,
          IsChecked_4: false
        }
      },
      rules: {
        GroupPlaceType: [{
          required: true,
          message: "此处不可为空",
          trigger: "blur"
        }, {
          min: 1,
          message: "此处不可为空",
          trigger: "change",
          type: "number"
        }],
        GroupTravelType: [{
          required: true,
          message: "此处不可为空",
          trigger: "blur"
        }, {
          min: 1,
          message: "此处不可为空",
          trigger: "change",
          type: "number"
        }],
        IsJoinGroup: [{
          required: true,
          message: "此处不可为空",
          trigger: "change"
        }, {
          min: 0,
          message: "此处不可为空",
          trigger: "change",
          type: "number"
        }],
        ContactsList: [{
          required: true,
          type: "array",
          message: "此处不可为空",
          trigger: "change"
        }]
      }
    };
  },
  mounted() {
    this.loaddatas();
    this.loadcontacts();
  },
  methods: {
    loadcontacts() {
      let that = this;
      httputils
        .get(
          "/travel/supergoproduct/ProductSupplier/QueryProductSupplierInfo", {
            supplierId: $("#SupplierId").val()
          }
        )
        .then(function (resp) {
          if (resp) {
            that.contactsource = resp;
          }
        });
    },
    contactchange(val, index) {
      let that = this;
      that.contactsource.forEach(item => {
        if (item.Phone == val) {
          that.getFormData.ContactsList[index].ContactName = item.Name
          that.getFormData.ContactsList[index].LinkerId = item.LinkmanId
        }
      });
    },
    choosehotel(index) {
      this.$refs["choosehotel"].opendialog(index);
    },
    onselected(hotel, index) {
      if (!index || index <= -1) {
        return;
      } else {
        that.getFormData.HotelList[index].HotelName = hotel.HotelName;
        that.getFormData.HotelList[index].HotelAddress = hotel.Address;
        that.getFormData.HotelList[index].Intro = hotel.HotelIntro;
      }
    },
    editnights(hotel, index) {
      this.$refs["choosenight"].opendialog(
        $("#Nights").val(),
        hotel.DayOfTrip,
        index
      );
    },
    onconfirm(nights, index) {
      let that = this;
      if (!index || index <= -1) {
        if (!that.getFormData.HotelList) {
          that.getFormData.HotelList = [];
        }
        that.getFormData.HotelList.push({
          DayOfTrip: nights,
          HotelName: "",
          HotelAddress: "",
          Intro: ""
        });
      } else {
        that.getFormData.HotelList[index].DayOfTrip = nights;
      }
    },
    addhotel() {
      this.$refs["choosenight"].opendialog($("#Nights").val(), "");
    },
    ckGroupVal1: function () {
      if (!/^[1-9]\d*$/.test(this.getFormData.GroupDetail.JoinGroupNum_1)) {
        this.getFormData.GroupDetail.JoinGroupNum_1 = "1";
      }
    },
    ckGroupVal2: function () {
      if (!/^[1-9]\d*$/.test(this.getFormData.GroupDetail.JoinGroupNum_2)) {
        this.getFormData.GroupDetail.JoinGroupNum_2 = "1";
      }
    },
    removeContact: function (contactToRemove) {
      let that = this;
      if (this.getFormData.ContactsList.length <= 2) {
        that.$message({
          type: "error",
          message: "紧急联系人至少两个!"
        });
        return;
      }
      var index = this.getFormData.ContactsList.indexOf(contactToRemove);
      this.getFormData.ContactsList.splice(index, 1);
    },
    addContact: function () {
      let that = this;
      if (this.getFormData.ContactsList.length >= 3) {
        that.$message({
          type: "error",
          message: "紧急联系人最多三个!"
        });
        return;
      }
      this.getFormData.ContactsList.push({
        Type: "",
        ContactName: "",
        ContactMobile: ""
      });
    },
    /**
     * 删除交通信息
     */
    RemoveTraffic: function (index) {
      if (!this.getFormData.TrafficList) {
        this.getFormData.TrafficList = [];
      }
      this.getFormData.TrafficList.splice(index, 1);
    },
    /**
     * 添加交通信息
     */
    addTraffic: function () {
      if (!this.getFormData.TrafficList) {
        this.getFormData.TrafficList = [];
      }
      this.getFormData.TrafficList.push({
        TripNo: "",
        TrafficType: "",
        TrafficNumber: "",
        DeptCity: "",
        DeptStation: "",
        DeptTime: "",
        DestCity: "",
        DestStation: "",
        DestTime: "",
        Description: ""
      });
    },
    GetFlight: function (
      index //填充航班信息
    ) {
      let that = this;
      var myDate = new Date();
      if (!this.getFormData.TrafficList[index].TrafficNumber == "") {
        that.$message({
          type: "error",
          message: "请输入班次!"
        });
        return;
      }
      httputils
        .post("/travel/supergoproduct/ProductReferInfo/GetFlightInfo", {
          FlightNum: this.getFormData.TrafficList[index].TrafficNumber,
          flightTime: myDate.toLocaleDateString()
        })
        .then(data => {
          if (data.IsSuccess) {
            var tripNo = that.getFormData.TrafficList[index].TripNo;
            that.getFormData.TrafficList.splice(index, 1);
            for (var i = 0; i < data.Result.length; i++) {
              var _obj = {
                TripNo: tripNo,
                TrafficType: "飞机",
                TrafficNumber: data.Result[i].FlightNum,
                DeptCity: data.Result[i].StartCity,
                DeptStation: data.Result[i].StartAirport,
                DeptTime: data.Result[i].FlightTime,
                DestCity: data.Result[i].ArriveCity,
                DestStation: data.Result[i].ArriveAirport,
                DestTime: data.Result[i].PredictArriveTime
              };
              that.getFormData.TrafficList.push(_obj);
              that.getFormData.TrafficList.sort(function (a, b) {
                return a.TripNo - b.TripNo;
              });
            }
          } else {
            that.$message({
              type: "error",
              message: "该航班信息不存在或经停航班不可带出!"
            });
            return;
          }
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: err
          });
        });
    },
    //删除酒店
    removeHotel: function (index) {
      this.getFormData.HotelList.splice(index, 1);
    },
    AddVisaInfo: function () {
      if (!this.getFormData.VisaInfo.PrePareInfoList) {
        this.getFormData.VisaInfo.PrePareInfoList = [];
      }
      this.getFormData.VisaInfo.PrePareInfoList.push({
        People: "",
        SpecInfo: "",
        ImgUrl: ""
      });
    },
    removeVisa: function (index) {
      this.getFormData.VisaInfo.PrePareInfoList.splice(index, 1);
    },
    btnAddImage: function (index) {
      this.uploadImg($event);
    },
    uploadImg: function (e, visa) {
      let that = this;
      let tag = e.target;
      let fileList = tag.files;
      let imgNum = fileList.length;
      var file = fileList[0];
      if (imgNum > 1) {
        that.$message({
          type: "error",
          message: "每次请只上传一个文件！"
        });
        return;
      }
      if (file.type.split("/")[0] != "image") {
        that.$message({
          type: "error",
          message: "只允许上传图片！"
        });
        return;
      }
      var file_size = file.size / 1024;
      if (file_size > 4000) {
        that.$message({
          type: "error",
          message: "文件大小请不要超过4M！"
        });
        return;
      }
      this.createImage(file, visa);
    },
    createImage: function (file, visa) {
      var image = new Image();
      var reader = new FileReader();
      let param = new FormData();
      param.append("file", file, file.Name);
      reader.onload = ex => {
        var image = ex.target.result;
        axios
          .post("/travel/supergoproduct/Common/UploadImage", param, {
            headers: {
              "Content-Type": "multipart/form-data"
            }
          })
          .then(function (response) {
            console.log(response.data);
            visa.ImgUrl = response.data.Result[0];
          });
      };
      reader.readAsDataURL(file);
    },

    /**
     * 加载数据
     */
    loaddatas() {
      let that = this;
      var loading = that.$loading({
        lock: true
      });
      httputils
        .get("/travel/supergoproduct/ProductReferInfo/GetProductReferInfo", {
          ProductId: $("#productid").val()
        })
        .then(result => {
          loading && loading.close();
          that.getFormData = result;
        })
        .catch(err => {
          that.$message({
            type: "error",
            message: err
          });
          loading && loading.close();
        });
    },
    /**
     * 保存数据
     */
    saveDatas() {
      let that = this;
      this.$refs["refermain"].validate(function (valid) {
        if (valid) {
          var loading = that.$loading({
            lock: true
          });
          httputils
            .post(
              "/travel/supergoproduct/ProductReferInfo/SaveProductReferInfo",
              Object.assign(that.getFormData, {
                ProductId: $("#productid").val()
              })
            )
            .then(result => {
              loading && loading.close();
              if (result && result.IsSuccess) {
                that.$confirm(result.ErrorMsg, "保存成功", {
                  confirmButtonText: "确定",
                  type: "warning",
                  center: true,
                  showCancelButton: false
                });
              } else {
                that.$alert(result.ErrorMsg, "保存失败", {
                  dangerouslyUseHTMLString: true,
                  type: "warning",
                  center: true
                });
              }
            })
            .catch(err => {
              that.$message({
                type: "error",
                message: err
              });
              loading && loading.close();
            });
        }
      });
    }
  }
});

$(function () {
  $("#saveProductInfo").click(function () {
    app.saveDatas();
  });
});



