<template>
    <div class="imageuploader">
        <glui-upload list-type='picture-card' class="avatar-uploader" action="/travel/supergoproduct/Common/UploadImage" :show-file-list="false" :on-success="onsuccess">
            <img v-if="url" :src="url" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </glui-upload>
        <glui-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="url" alt="">
        </glui-dialog>
        <div v-if="url" class="uploadimage__item-actions">
            <span class="uploadimage__item-preview" v-on:click='onpreview'>
                <i class="el-icon-zoom-in"></i>
            </span>
            <span class="uploadimage__item-delete" v-on:click="onremove">
                <i class="el-icon-delete"></i>
            </span>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            dialogVisible: false
        }
    },
    props: {
        url: {
            type: [String],
            default() {
                return "";
            }
        }
    },
    methods: {
        /**
         * 上传成功
         */
        onsuccess(response, file, fileList) {
            let that = this;
            if (response && response.Result && response.Result.length >= 1) {
                that.$emit("update:url", response.Result[0]);
            }
        },
        /**
         * 删除
         */
        onremove() {
            let that = this;
            that.$emit("update:url", "");
        },
        /**
         * 预览
         */
        onpreview() {
            this.dialogVisible = true;
        }
    }
}
</script>

<style scoped>
.imageuploader {
  width: 148px;
  height: 148px;
  line-height: 148px;
  position: relative;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  width: 100%;
  height: 100%;
  font-size: 28px;
  color: #8c939d;
  text-align: center;
}
.avatar {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.imageuploader .uploadimage__item-actions {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  cursor: default;
  text-align: center;
  color: #fff;
  opacity: 0;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s;
  z-index: 100;
}
.imageuploader .uploadimage__item-actions:hover {
  opacity: 1;
}
.imageuploader .uploadimage__item-actions span {
  color: #fff;
  cursor: pointer;
}
.imageuploader .uploadimage__item-actions i {
  color: #fff;
  font-size: 20px;
}
.imageuploader .uploadimage__item-actions .uploadimage__item-delete {
  position: static;
  font-size: inherit;
  color: inherit;
}
</style>



