<template>
    <glui-dialog title="选择酒店" :visible.sync="dialogTableVisible"  width="90%">
        <glui-form :model="searchForm" label-width="100px">
            <glui-row>
                <glui-col :span="7">
                    <glui-form-item label="酒店编号">
                        <glui-input v-model="searchForm.ResourceId" placeholder="请输入酒店编号"></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="7">
                    <glui-form-item label="酒店名称">
                        <glui-input v-model="searchForm.HotelName" placeholder="请输入酒店名称"></glui-input>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="7">
                    <glui-form-item label="城市">
                        <dropdowncity v-model="searchForm.CityId" :isinner="false" clearable></dropdowncity>
                    </glui-form-item>
                </glui-col>
                <glui-col :span="3">
                    <glui-form-item>
                        <div class="pull-right">
                            <button class="btn btn-default" type="button" id="js_query" v-on:click="searchDatas">
                                <i class="bico bico-search"></i>查&nbsp;询
                            </button>
                        </div>
                    </glui-form-item>
                </glui-col>
            </glui-row>
        </glui-form>
        <glui-table :data="searchResult" row-key='ResourceId'>
            <glui-table-column prop="ResourceId" label="酒店编号" width="80">
            </glui-table-column>
            <glui-table-column prop="HotelName" label="酒店名称">
            </glui-table-column>
            <glui-table-column prop="CityName" label="所在城市">
            </glui-table-column>
            <glui-table-column prop="Address" label="酒店地址">
            </glui-table-column>
            <glui-table-column prop="StarLevel" label="酒店星级">
            </glui-table-column>
            <glui-table-column prop="HotelIntro" label="酒店介绍">
            </glui-table-column>
            <glui-table-column label="操作">
                <template slot-scope="scope">
                    <a href="javascript:void(0)" v-on:click="selecthotel(scope.row)">选择</a>
                </template>
            </glui-table-column>
        </glui-table>
        <glui-pagination v-on:size-change="sizeChange" v-on:current-change="pageChange" :page-sizes="[10,20]" layout="total, sizes, prev, pager, next, jumper" style="text-align:center;margin-top:14px;" :page-size="searchForm.pageSize" :total="total">
        </glui-pagination>
    </glui-dialog>
</template>
<script>
import httputils from "@/js/httputils";
import dropdowncity from "@/components/dropdowncity";
export default {
    components: {
        dropdowncity
    },
    data() {
        return {
            total: 0,
            hotelindex: -1,
            dialogTableVisible: false,
            searchForm: {
                pageSize: 10,
                PageIndex: 1,
                CityId: "",
                ResourceId: "",
                HotelName: ""
            },
            searchResult: [],
        }
    },
    methods: {
        sizeChange: function (val) {
            this.searchForm.pageSize = val;
            this.searchDatas();
        },
        pageChange: function (val) {
            this.searchForm.PageIndex = val;
            this.searchDatas();
        },
        opendialog(index) {
            this.hotelindex = index;
            this.dialogTableVisible = true;
        },
        searchDatas() {
            let that = this;
            httputils.get('/travel/supergoproduct/ProductTrip/GetJourneyHotelList', that.searchForm)
                .then((result) => {
                    if (result && result.IsSuccess) {
                        that.total = result.Total;
                        that.searchResult = result.Content;
                    } else {
                        that.total = 0;
                        that.searchResult = [];
                    }
                }).catch((err) => {
                    that.$message({
                        type: 'error',
                        message: err
                    });
                });
        },
        selecthotel(hotel) {
            let that = this;
            that.$emit("onselected", hotel, that.hotelindex);
            that.dialogTableVisible = false;
        }
    }
}
</script>



