<template>
    <glui-dialog title="酒店入住晚数" :visible.sync="dialogTableVisible">
        <glui-form :model="formData" :rules="rules" ref="choosedialog" label-width="130px">
            <glui-form-item props='checkList' label='入住晚数'>
                <glui-checkbox-group v-model="formData.checkList">
                    <glui-checkbox v-for="item in nightSource" :label="item" :key="item">
                        第{{item}}晚
                    </glui-checkbox>
                </glui-checkbox-group>
            </glui-form-item>
            <glui-form-item>
                <glui-button size="small" type="primary" v-on:click='confirmData'>确认选择</glui-button>
            </glui-form-item>
        </glui-form>
    </glui-dialog>
</template>
<script>
export default {
    data() {
        return {
            hotelindex: -1,
            dialogTableVisible: false,
            nightSource: [1],
            formData: {
                checkList: []
            },
            rules: {
                checkList: [{
                    required: true,
                    message: "未选择酒店入住晚数",
                    trigger: "change"
                }]
            }
        }
    },
    methods: {
        opendialog(nights, checknights, index) {
            this.dialogTableVisible = true;
            this.hotelindex = index ? index : -1;
            if (!nights && (nights instanceof String)) {
                this.nightSource = nights.split(',');
            }
            if (!checknights && (checknights instanceof String)) {
                this.formData.checkList = checknights.split(',');
            } else {
                this.formData.checkList = [];
            }
        },
        confirmData() {
            let that = this;
            that.$refs["choosedialog"].validate(function (valid) {
                if (!valid) {
                    that.$message({
                        type: 'error',
                        message: "未选择酒店入住晚数"
                    });
                    return;
                }
                that.$emit("onconfirm", that.formData.checkList.join(','), that.hotelindex);
                that.dialogTableVisible = false;
            });
        }
    }
}
</script>



